<template>
  <div>
    <slot></slot>
  </div>
</template>

<script>
import qs from 'qs'
import { Toast } from 'vant'
import { isIOS, transferBytes } from '@/utils/tools'

const getRotate = () => {
  return import('@/utils/rotateImg.js')
}

let accessid = ''
let host = ''
let policyBase64 = ''
let signature = ''
let callbackbody = ''
let callbackVar = ''
let filename = ''
let key = ''
let expire = 0
let objectName = ''

export default {
  props: [
    'container',
    'browse_button',
    'actionFile',
    'params',
    'args'
  ],
  watch: {
    actionFile (val) {
      if (val) {
        let reader = new FileReader()
        let fileByteArray = []
        reader.readAsArrayBuffer(val)

        let params = {
          ...this.set_upload_param(''),
          file: val
        }
        let arr = Object.keys(params)
        let formData = new FormData()
        Toast.loading({
          duration: 0,
          forbidClick: true,
          message: ''
        })

        arr.forEach(element => formData.append(element, params[element]))
        let picId = null
        this.$http('http://hello-image-test.oss-cn-hangzhou.aliyuncs.com/', formData).then(info => {
          if (info.status === 'OK') {
            picId = info.picId

            this.$http(`/api/member/image/url`, { picId }, 'get').then(res => {
              if (res.code === '0000') {
                let { url } = res.data
                this.$emit('uploadSucess', {
                  ...res.data,
                  args: this.args
                })
              }
            })
          }
        }).finally(() => {
          Toast.clear()
        })
      }
    }
  },

  methods: {

    set_upload_param (filename) {
      this.get_signature()
      objectName = key

      let newMultipartParams = {
        'key': objectName,
        'policy': policyBase64,
        'OSSAccessKeyId': accessid,
        'success_action_status': '200', // 让服务端返回200,不然，默认会返回204
        'callback': callbackbody,
        'signature': signature,
        ...callbackVar
      }
      return newMultipartParams
    },
    sendRequest () {
      var xmlhttp = null
      if (window.XMLHttpRequest) {
        xmlhttp = new XMLHttpRequest()
      } else if (window.ActiveXObject) {
        // eslint-disable-next-line no-undef
        xmlhttp = new ActiveXObject('Microsoft.XMLHTTP')
      }
      if (xmlhttp != null) {
        let params = qs.stringify(this.params || {})
        let serverUrl = `/api/member/image/upload/policy?${params}`

        xmlhttp.open('GET', serverUrl, false)
        xmlhttp.send(null)
        return xmlhttp.responseText
      } else {
        console.log('Your browser does not support XMLHTTP.')
      }
    },

    get_signature () {
      // 可以判断当前expire是否超过了当前时间， 如果超过了当前时间， 就重新取一下，3s 作为缓冲。
      let body = this.sendRequest()
      let data = JSON.parse(body)
      var obj = data.data
      host = obj['host']
      policyBase64 = obj['policy']
      accessid = obj['accessid']
      signature = obj['signature']
      expire = parseInt(obj['expire'])
      callbackbody = obj['callback']
      callbackVar = obj['callbackVar']
      key = obj['key']
    }
  },
  mounted () {
    let _this = this
    this.$nextTick(e => {
      let container = document.getElementById(this.browse_button)
      container.style.cssText = 'position: relative;overflow: hidden;display:block;'

      let input = document.createElement('input')
      input.setAttribute('type', 'file')
      input.setAttribute('id', this.browse_button)
      input.setAttribute('accept', 'image/*')
      input.setAttribute('class', 'hideInputFile')

      input.addEventListener('change', (e) => {
        let target = e.target
        let file = target.files[0]
        if (isIOS()) {
          return getRotate().then(({ rotatePhoto }) => {
            rotatePhoto(file, (res) => {
              this.$emit('fileChange', {
                file: res,
                args: this.args
              })
            })
          })
        }
        this.$emit('fileChange', {
          file: file,
          args: this.args
        })
      }, false)
      container.appendChild(input)
      // console.log('container===?', container)
    })
  }
}
</script>

<style lang="scss">
.hideInputFile {
  font-size: 12px;
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
</style>
